.pageContainer {
  background: #edf3f2;
  background: linear-gradient(107deg, rgba(237,243,242,1) 20%, rgba(255,255,255,1) 50%, rgba(237,243,242,1) 80%);
  z-index: 20;
  width: 89%;
  margin: 0 0 0 11%;
  padding: 0;
  position: relative;
  height: 100%;
  font-family: Raleway, sans-serif;
  color: var(--text-color);
}

.flexColumn {
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bigText {
  text-transform: uppercase;
  color: var(--attention-color);
  font-weight: bolder;
  font-size: 24px;
  line-height: 1;
  padding-right: 5px;
}

.roundedLine {
  border-top: 5px solid var(--secondary-color);
  border-radius: 5px;
  margin-top: 10vh;
}

.expand {
  -webkit-animation: expand 0.5s ease-in forwards;
          animation: expand 0.5s ease-in forwards;
}

@-webkit-keyframes expand {
  0% {
      width: 0px
  }
  100% {
      width: 26%;
  }
}

@keyframes expand {
  0% {
      width: 0px
  }
  100% {
      width: 26%;
  }
}

.pop {
  -webkit-animation: pop 0.4s forwards ease-in;
          animation: pop 0.4s forwards ease-in;
}

.actionButton {
  cursor: pointer;
  padding: 20px;
  font-size: 20px;
  background-color: var(--attention-color);
  border-radius: 10px;
  border: none;
  box-shadow: 2px 2px 5px black;
  transition: 0.1s ease-in;
  font-weight: bold;
  font-family: Roboto, sans-serif;
  color: var(--text-color)
}

.actionButton:hover {
  -webkit-transform: translateY(2%);
          transform: translateY(2%);
  background-color: rgb(190, 29, 61);
}
  
@-webkit-keyframes pop {
  0% {
    opacity: 0;
    -webkit-transform: translateY(1%);
            transform: translateY(1%);
    }
  
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    }
}
  
@keyframes pop {
  0% {
    opacity: 0;
    -webkit-transform: translateY(1%);
            transform: translateY(1%);
    }
  
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    }
}

a {
